
#app {
  background: #ffffff;
}
































/* header */
#nbar {
  background: rgba(255,255,255,0.5);
  -webkit-box-shadow: 0 0 5px #e6ffde;
          box-shadow: 0 0 5px #e6ffde;
}
#navbar {
  max-width: 100%;
}
#logo {
  width: 150px;
  height: 50px;
  margin-left: 30px;
}
#navbarNav {
  margin-right: 2%
}








































































































































































/* sobre */
#corpo {
  color: #717878;
  margin-top: 5.2%;
  width: 100%;
}
#sobre {
  width: auto;
  height: auto;
  background: #e6ffde;
  padding-top: 3%;
  padding-bottom: 7%;
  margin-bottom: 5%;
}
#descricao {
  padding-left: 10%;
  padding-top: 5rem;
  padding-bottom: 5rem;
  padding-right: 5%;
}
#linha-header {
  height: 3px;
  background-color: #fff;
  border-radius: 20%;
  margin-left: 10%;
  margin-bottom: 5%;
  margin-top: 5%;
  margin-right: 15%;
}
/* border: 3px solid #e2e5e5; */
#image-eu {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 5px #606868;
          box-shadow: 0 0 5px #606868;
}
/* projetos */
#port {
  padding: 5%;
  -webkit-box-shadow: 1px 1px 20px #e6ffde;
          box-shadow: 1px 1px 20px #e6ffde;
  padding-bottom: 8%;
  margin-bottom: 1%;
}
#img-port {
  max-width: 40%;
  height: auto;
}
/* CONTATO */
#contato {
  padding-top: 8%;
  /* box-shadow: 1px 1px 20px #606868; */
}
#contato-text {
  text-align: center;
  margin-bottom: 8rem;
}
#descricao-contato {
  text-align: center;
}


#footer {
  background-color: #030303;
  height: auto;
  color: #e2e5e5;
}
#rodape-social {
  padding: 0;
  font-size: 30px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
#social {
  color: #e2e5e5;
  max-width: 100%;
  height: auto;
  padding-right: 1rem;
}
#social:hover {
  color: #fff;
  font-size: 35px;
  color: #fff;
}

